@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:300,400&display=swap);
html {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  padding: 50px;
  font-family: 'Source Code Pro';
  font-weight: 300; }
  .App .header {
    margin-bottom: 40px;
    font-family: 'Source Code Pro', sans-serif;
    font-size: 3rem;
    font-weight: 400; }
  .App .subtitle {
    font-size: 2rem;
    margin: 30px auto; }
    .App .subtitle .suffix {
      color: #ff8400;
      font-style: italic; }
      .App .subtitle .suffix span {
        margin-right: 20px; }
  .App form {
    margin: 20px; }
    .App form input {
      background-color: inherit;
      color: white;
      outline: none;
      border: solid 1px white;
      border-radius: 15px;
      padding: 15px 25px;
      font-size: 3rem; }
    .App form button {
      margin-left: -60px;
      background-color: inherit;
      border: none;
      outline: none;
      color: white;
      font-size: 3rem;
      cursor: pointer; }
  .App button {
    background-color: #ff7600;
    border: none;
    outline: none;
    color: white;
    cursor: pointer;
    padding: 5px 10px; }
  .App .errorMessage {
    margin: 20px auto;
    height: 20px;
    font-size: 1.5rem;
    color: red; }
  .App .scoreContainer {
    font-size: 3rem;
    margin: 50px auto; }
    .App .scoreContainer .speed {
      font-size: 1.5rem;
      color: #5eff00; }

